body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.menu {
    height: 40px;
}


.general {
    background-color: #292E49;
    width: 100%;
    margin-bottom: 10px;
}

.landing-style {
    background-color: transparent;
    width: 100%;
    margin-bottom: 10px;
}

.landing-style .general li {
    width: 100px;
}

.landing-style li a,
.general li a {
    color: white;
    text-align: center;
}

.landing-style .pure-menu-active > .pure-menu-link, .pure-menu-link:hover, .pure-menu-link:focus,
.general .pure-menu-active > .pure-menu-link, .pure-menu-link:hover, .pure-menu-link:focus {
    background-color: transparent;

}

.landing-style li a:hover,
.general li a:hover {
    background-color: none;
}

.landing-style li::before,
.general li::before  {
    content: "";
    position: absolute;
    width: 100%;
    height: 2px;
    bottom: 0;
    left: 0;
    background-color: white;
    visibility: hidden;
    transform: scaleX(0);
    transition: all .35s ease-in-out;
}

.landing-style li:hover::before,
.general li:hover::before {
    visibility: visible;
    transform: scaleX(1);
}

html, body {
    background-color: white;
    width: 100%;
    height: 100%;
    margin: 0;
}

.content {
    font-family: 'Open Sans', serif;
    font-size: 1rem;
    padding: 1.5em;
}

.search-style {
    width: 100%;
}
#react-paginate ul {
    display: inline-block;
    padding-left: 15px;
    padding-right: 15px;
}

#react-paginate li {
    display: inline-block;
}

#react-paginate a {
    color: black;
    float: left;
    padding: 8px 16px;
    text-decoration: none;
    transition: #ddd .3s;
    border: 1px solid #ddd;
  }
    
  #react-paginate a:hover:not(.active) {background-color: #ddd;}
/*This removes right margin from ul list */

ul.package-list  {
    -webkit-padding-start: 0;
    -webkit-margin-before: 0;
            margin-block-start: 0;
}
.landing {
    background: #536976;
    /* fallback for old browsers */
    /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #292E49, #536976);
    /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.title {
    font-family: 'Lobster', cursive;
    font-size: 2em;
}
.content {
    font-family: 'Open Sans', serif;
    font-size: 1rem;
    padding: 1.5em;
}

.l-box {
    padding: 1.5em;
}

.right-column h5 {
    color: rgba(0, 0, 0, 0.5);
    margin: 20px 0 20px 0;
}

.right-column p {
    font-size: 1.25rem;
}

.clipboard-copy {
    resize: none;
}

.right-column ul {
    list-style-type: none;
}

.right-column ul#collaborators {
    padding: 0;
    margin: 0;
}

.right-column ul#collaborators li {
    display: inline;
    margin-left: 10px;
}

.right-column ul#collaborators li:first-of-type {
    margin-left: 0px;
}

.right-column ul#collaborators img {
    margin-bottom: 10px;
}

.left-column p {
    color: rgba(0, 0, 0, .85);
    line-height: 1.6;
    margin-top: 0;
    margin-bottom: 16px;
    line-height: 1.65;
    letter-spacing: 0.1px;
}

pre {
    background-color: #f7f7f7;
    display: block;
    font-family: 'Inconsolata', monospace;
    white-space: pre;
    margin: 1em 0;
}

.markdown>h1, .markdown h2 {
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    margin: 8px 0 16px 0;
    padding-bottom: 8px;
}

.markdown h2 {
    letter-spacing: -0.3px;
    line-height: 1.25;
    font-size: 1.5rem;
}

.source-links {
    font-weight: bold;
}

.right-column {
    word-wrap:break-word;
}
